*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

body{
    font-family: Verdana, Geneva, Tahoma, sans-serif;

}

.app{
    background-image: url('./photo/cold.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease;
}
.app.warm{
    background-image: url('./photo/warms.jpg');
}

main{
    min-height: 100vh;
    padding: 25px;
    background-image: linear-gradient(to bottom ,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
}

.search-box{
    width: 100%;
    margin: 0 0 75px;

}

.search-box .search-bar{
    display: block;
    width: 100%;
    background: none;
    padding: 15px;
    appearance: none;
    outline: none;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 0 0 16px 16px;
    margin-top: -25px;
    box-shadow: 0 5px rgba(0,0,0,0.2);
    color: #313131;
    font-size: 23px;
    transition: 0.4s;

}

.search-box .search-bar:focus{
    background-color: rgba(255,255,255,0.75);

}

.location-box .location{
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.location-box .date{
    color: #fff;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-shadow: 2px 2px rgba(50,50,70,0.5);
    text-align: center;
}

.weather-box{
    text-align: center;

}
.weather-box .temp{
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    color: #fff;
    font-size: 102px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);

}

.weather-box .weather{
    color: #fff;
    text-shadow:3px 3px rgba(50, 50, 70, 0.5);
    font-size: 48px;
    font-weight: 700;
}


